.alarm-content {
  /*.handsontable th:last-child .changeType {
    display: none;
  }*/
  /*table*/
  /*myTree*/
  /* default theme */
}
.alarm-content .add_back0 {
    background: #fff !important;
}
.alarm-content .add_back1 {
    background: #dee2ed !important;
}
.alarm-content .contentArea {
    position: relative;
    padding: 20px 32px 0 32px;
    overflow: hidden;
}
.alarm-content .myTable {
    width: 100%;
    height: calc(100% - 20px);
    background: #dee2ed;
}
.alarm-content #tableContent {
    height: calc(100% - 40px);
}
.alarm-content .noData {
    width: 100%;
    position: absolute;
    top: 50%;
    left: 0;
    font-size: 18px;
    text-align: center;
}
.alarm-content .create-btn {
    position: absolute;
    top: 30px;
    right: 300px;
    z-index: 999;
    font-size: 16px;
    color: #000;
    width: 112px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    border: 1px solid #fff;
    border-radius: 3px;
    background: #fff;
}
.alarm-content .toggle-btn, .alarm-content .keep-btn {
    position: absolute;
    top: 30px;
    right: 60px;
    font-size: 16px;
    color: #fff;
    width: 112px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    border: 1px solid #fff;
    border-radius: 3px;
    background: transparent;
}
.alarm-content .toggle-btn:hover, .alarm-content .keep-btn:hover {
      background: #fff;
      color: #000;
}
.alarm-content .keep-btn {
    right: 50px;
}
.alarm-content .handsontable tr {
    background: #888;
}
.alarm-content .handsontable th {
    background-color: #fff;
    color: #000;
    line-height: 40px;
    font-size: 16px;
}
.alarm-content .htCore thead th {
    line-height: 40px;
}
.alarm-content .handsontable thead th .relative {
    padding: 0 4px;
}
.alarm-content .htCore tbody th {
    line-height: 31px;
}
.alarm-content .handsontable td {
    border-color: #888;
    line-height: 30px;
    text-align: center;
    font-size: 14px;
}
.alarm-content .handsontable .changeType {
    background: transparent;
    border-radius: 0;
    border: none;
    color: #bbb;
    font-size: 9px;
    line-height: 9px;
    padding: 2px;
    margin: 12px 1px 0 5px;
    float: right;
}
.alarm-content .handsontable .changeType:hover {
    border: none;
}
.alarm-content .handsontable .columnSorting.sortAction:hover {
    text-decoration: none;
    cursor: pointer;
}
.alarm-content .handsontable thead th {
    border-color: #888;
}
.alarm-content .handsontable tr:first-child td, .alarm-content .handsontable tr:first-child th {
    border-top: 1px solid #888;
}
.alarm-content .handsontable th:last-child {
    border-right: 1px solid #888;
    border-bottom: 1px solid #888;
}
.alarm-content .handsontable th:first-child {
    border-left: 1px solid #888;
}
.alarm-content .handsontable .htNoFrame + td, .alarm-content .handsontable .htNoFrame + th, .alarm-content .handsontable.htRowHeaders thead tr th:nth-child(2), .alarm-content .handsontable td:first-of-type, .alarm-content .handsontable th:first-child, .alarm-content .handsontable th:nth-child(2) {
    border-left: 1px solid #888;
}
.alarm-content .handsontable.listbox tr:first-child td {
    border-color: #888;
    border-top-color: transparent;
}
.alarm-content .handsontable.listbox td {
    border-color: #888;
}
.alarm-content .controls-btn {
    height: 58px;
    border: 1px solid #888;
    padding-left: 15px;
    background: #fff;
    border-radius: 7px;
}
.alarm-content .myTree {
    width: 100%;
    height: calc(100% - 50px);
    overflow: hidden;
}
.alarm-content #jsmind_container {
    width: 100%;
    height: 100%;
    background: #DEE2ED;
}
.alarm-content .jsmind-inner::-webkit-scrollbar {
    width: 10px;
    height: 1px;
}
.alarm-content .jsmind-inner::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    background: #535353;
}
.alarm-content .jsmind-inner::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    background: #EDEDED;
}
.alarm-content .right-click {
    width: 150px;
    height: 200px;
    position: absolute;
    z-index: 9;
    display: none;
    background: #fff;
    border: 1px solid #ccc;
}
.alarm-content .right-click a {
      display: block;
      height: 34px;
      font-size: 15px;
      line-height: 34px;
      text-align: center;
      color: #798CA1;
}
.alarm-content .right-click a:hover {
        background: #f5f5f5;
}
.alarm-content jmnode:hover, .alarm-content button:hover {
    cursor: pointer;
}
.alarm-content .up {
    width: 0;
    height: 0;
    border-right: 10px solid transparent;
    border-left: 10px solid transparent;
    border-bottom: 10px solid #6699FF;
}
.alarm-content .up:hover {
      cursor: pointer;
      border-bottom-color: #3377ff;
}
.alarm-content .down {
    width: 0;
    height: 0;
    border-right: 10px solid transparent;
    border-left: 10px solid transparent;
    border-top: 10px solid #6699FF;
}
.alarm-content .down:hover {
      cursor: pointer;
      border-top-color: #3377ff;
}
.alarm-content .right {
    width: 0;
    height: 0;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-left: 10px solid #6699FF;
}
.alarm-content .right:hover {
      cursor: pointer;
      border-left-color: #3377ff;
}
.alarm-content jmnode {
    padding: 15px 28px;
    background-color: #fff;
    color: #798CA1;
    border-radius: 4px;
    box-shadow: 1px 1px 1px #666;
    font: 16px MicrosoftYaHei;
}
.alarm-content jmnode:hover {
    box-shadow: 2px 2px 8px #000;
    background-color: #ebebeb;
    color: #333;
}
.alarm-content jmnode.selected {
    background-color: #fff;
    color: #6699FF;
    box-shadow: 2px 2px 8px #6699FF;
}
.alarm-content jmnode.root {
    font-size: 16px;
}
.alarm-content jmexpander {
    border-color: #6699FF;
}
.alarm-content jmexpander:hover {
    border-color: #6699FF;
}
.alarm-content jmexpander {
    line-height: 9px;
    font-size: 16px;
    color: #6699FF;
}
.alarm-content {
  position: relative;
}
